/* Style the product card container */
.product-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 200px; /* Fixed card width */
    padding: 20px;
    margin: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #fff;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  }
  
  /* Style the thumbnail image */
  .product-thumbnail {
    width: 100%;
    /* max-height: 200px; */
    object-fit: cover;
    margin-bottom: 10px;
  }
  
  /* Style other product details */
  .product-details {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  
  .product-title {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .product-description {
    font-size: 14px;
    margin-bottom: 10px;
  }
  
  .product-price {
    font-size: 16px;
    color: #007bff;
    margin-bottom: 10px;
  }
  
  .product-discount {
    font-size: 14px;
    color: #ff5722;
    margin-bottom: 10px;
  }
  
  .product-stock {
    font-size: 14px;
    color: #4caf50;
  }
  
  /* Create a container for the card rows */
  .card-row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  