/* Apply a reset to remove default styling */
body, div, form, input, label, button {
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
    font-family: Arial, sans-serif;
  }
  
  /* Style the form container */
  div {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  
  /* Style the form */
  form {
    width: 100%;
    max-width: 400px;
    padding: 20px;
    background-color: #f7f7f7;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  /* Style form labels */
  label {
    display: block;
    margin-bottom: 6px;
    font-weight: bold;
  }
  
  /* Style form inputs */
  input {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  /* Style the submit button */
  button {
    background-color: #007bff;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  /* Media query for responsiveness */
  @media screen and (max-width: 600px) {
    form {
      max-width: 90%;
    }
  }
  
